/********************* CSS pour les labels *****************/
.SDK3DVerse-viewport-container {
  top: 4rem !important;
  height: calc(100vh - (4rem + 2px)) !important;
  width: 100% !important;
  vertical-align: middle !important;
}

.SDK3DVerse-viewport,
.SDK3DVerse-viewport>div {
  border: 0px !important;
  height: 100% !important;
}

.label-container {
  transform-origin: 50% 50%;
  width: 30px;
  height: 30px;
}

.label-container>.active,
.label-container> :hover {
  z-index: 10 !important;
}

.label-container.active .label,
.label-container:hover .label,
.label-container:focus .label {
  width: 80px;
  height: 40px;
  background-color: white;
  transform: translate(-50, -100%);
  transition: width 0.1s ease;
}

.label-container .label:hover+.label-title:not(:empty),
.label-container .label:focus+.label-title:not(:empty) {
  transform: scaleX(1);
  opacity: 1;
}

.label {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  pointer-events: all !important;
  cursor: pointer;
  transform: translate(-50%, -100%);
  border-radius: 20px;
  border: 8px;
  transform-origin: 50% 50%;
  padding: 0 3px 3px 0;
  width: 30px;
  height: 30px;
  background: white;
  background-size: contain;
  font-size: 0.75rem;
  line-height: 1rem;
  color: rgba(9, 11, 12, .6);
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -2.2em 0 0 -1.3em;
  -webkit-box-shadow: -1px 1px 4px rgba(0, 0, 0, .5);
  -moz-box-shadow: -1px 1px 4px rgba(0, 0, 0, .5);
  box-shadow: -1px 1px 4px rgba(0, 0, 0, .5);
  transition: opacity 0.25s, transform 0.25s ease;
}

.label::after {
  content: '';
  position: absolute;
  bottom: -25%;
  left: 50%;
  margin-left: -5px;
  /* Adjust the positioning of the triangle */
  border-width: 5px;
  border-style: solid;
  border-color: white transparent transparent transparent;
}

.label-title {
  position: absolute;
  display: flex;
  align-items: center;
  top: -12px;
  left: -12px;
  transform: scaleX(0.9);
  transform-origin: center left;
  width: max-content;
  max-width: 15rem;
  min-height: 24px;
  padding: 0.25rem 0.75rem 0.25rem 1.75rem;
  font-size: 0.75rem;
  line-height: 1rem;
  border-radius: 12px;
  color: rgba(9, 11, 12, .8);
  background-color: rgba(241, 241, 241, .9);
  transition: opacity 0.25s, transform 0.25s;
  opacity: 0;
  pointer-events: none;
}

.label-title:hover {
  z-index: 20 !important;
}

.actionModal {
  display: none;
  background-color: lightgreen;
}

.collect {
  z-index: 30 !important;
  margin-top: -37.5px;
  margin-left: -13px;
  width: 80%;
  height: 80%;
  transform: rotate(45deg);
  /* Adjust the angle as needed */
  transition: transform 0.5s ease;
}

.label-container:hover .collect {
  position: absolute;
  top: -7.5px;
  left: -25px;
  width: 26px;
  height: 26px;
}

.attendees,
.attendees-txt {
  position: absolute;
  display: none;
  margin-top: 5px;
  width: 0%;
  height: 0%;
  color: black;
  text-align: center;
  opacity: 1;
  transition: width 0.3s ease, height 0.3s ease, opacity 0.5s ease;
}

.attendees {
  top: -60px;
  left: -12.5px;
  font-weight: bold;
  font-size: 22px;
  z-index: 15 !important;
}

.attendees-txt {
  text-align: left !important;
  font-size: 8px;
  /* Initial font size */
  top: -35px;
  left: -12.5px;
  z-index: 15 !important;
}

.label-container.active .attendees,
.label-container.active .attendees-txt,
.label-container:hover .attendees,
.label-container:hover .attendees-txt,
.label-container:focus .attendees,
.label-container:focus .attendees-txt {
  display: block;
}

